import React from 'react';
import { connect } from 'react-redux'
import { getKysymykset } from '../reducers/kysymyksetReducer'


class Kysymykset extends React.Component {

    componentDidMount = async() => {
        const all = this.props.getKysymykset()
        console.log('all', all)
        const kysymykset = all.map(k => k.kysymys)
        console.log('kysymykset', kysymykset)
      } 

    render() {
        return(
            <div>
             moi   
            </div>
        )
    }
      
}
const mapStateToProps = (state) => {
    return {
     kysymykset: state.kysymykset
    }
  }
export default connect(
    mapStateToProps, 
    { getKysymykset }
  )(Kysymykset)


