import kysymysService from '../services/kysymys'

const reducer = (store = [], action) => {
    if (action.type === 'GET_ALL') {
      return action.data
    }
    return store
}

export const getKysymykset = () => {
    var kysymykset = []
    return async (dispatch) => {
        const all = kysymysService.getAll()
        
      dispatch({
        type: 'GET_ALL',
        data: kysymykset
      })
    }
  }

export default reducer



